.app {
  display: flex;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  font-family: sans-serif;
  /* Disable touch actions globally */
  touch-action: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Disable touch actions for all elements */
.app * {
  touch-action: none !important;
  -webkit-touch-callout: none !important;
  -webkit-user-select: none !important;
  -khtml-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}

/* Allow text selection for input fields and text areas */
.app input,
.app textarea,
.app [contenteditable="true"] {
  touch-action: manipulation !important;
  -webkit-user-select: text !important;
  -khtml-user-select: text !important;
  -moz-user-select: text !important;
  -ms-user-select: text !important;
  user-select: text !important;
}

.sidebar {
  background-color: #2d3436;
  color: white;
  position: relative;
  transition: width 0.3s;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 100vh;
  overflow-y: auto;
}

.sidebar.collapsed {
  width: 60px !important;
}

.collapse-btn {
  background: #636e72;
  border: none;
  color: white;
  padding: 10px;
  width: 100%;
  cursor: pointer;
}

.menu {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.menu li {
  padding: 15px 20px;
  cursor: pointer;
}

.menu li:hover {
  background-color: #636e72;
}

.resizer {
  width: 5px;
  background: #b2bec3;
  cursor: ew-resize;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
}

.main {
  flex-grow: 1;
  background: #f1f2f6;
  padding: 20px;
  overflow: hidden;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.collapse-btn {
  background: none;
  border: none;
  cursor: pointer;
  margin: 10px;
  font-size: 1.2em;
}

.sidebar.collapsed .menu li {
  text-align: center;
  padding: 15px 0;
  font-weight: bold;
}

.sidebar.collapsed .menu li {
  font-size: 1.2em;
}
.menu li.active {
  background-color: #00cec9; /* highlight color */
  font-weight: bold;
  color: white;
}

.sidebar.collapsed .menu li.active {
  background-color: #00cec9;
  color: white;
  font-weight: bold;
}

/* React Spreadsheet Custom Styles */
.custom-spreadsheet {
  font-family: 'Arial', sans-serif !important;
  font-size: 14px !important;
  overflow: visible !important;
  max-width: none !important;
  max-height: none !important;
}

.custom-spreadsheet .Spreadsheet__table {
  border-collapse: collapse !important;
  border: 1px solid #d0d7de !important;
  min-width: max-content !important;
  width: auto !important;
}

.custom-spreadsheet .Spreadsheet__cell {
  border: 1px solid #d0d7de !important;
  padding: 8px 12px !important;
  background-color: #ffffff !important;
  color: #24292f !important;
  font-size: 13px !important;
  font-weight: 400 !important;
  text-align: right !important;
  vertical-align: middle !important;
  min-width: 100px !important;
  width: 100px !important;
  height: 32px !important;
  white-space: nowrap !important;
}

.custom-spreadsheet .Spreadsheet__cell--selected {
  background-color: #0366d6 !important;
  color: white !important;
  border: 1px solid #0366d6 !important;
}

.custom-spreadsheet .Spreadsheet__cell--readonly {
  background-color: #f8f9fa !important;
  color: #6c757d !important;
  font-weight: 600 !important;
  text-align: center !important;
}

.custom-spreadsheet .Spreadsheet__cell--editing {
  background-color: #ffffff !important;
  color: #24292f !important;
  border: 2px solid #0366d6 !important;
  border-radius: 4px !important;
}

.custom-spreadsheet .Spreadsheet__cell--editing input {
  background-color: transparent !important;
  border: none !important;
  outline: none !important;
  font-family: 'Arial', sans-serif !important;
  font-size: inherit !important;
  color: #24292f !important;
  width: 100% !important;
  height: 100% !important;
  padding: 0 !important;
}

.custom-spreadsheet .Spreadsheet__header {
  background-color: #f5f5f5 !important;
  color: #24292f !important;
  font-weight: 600 !important;
  text-align: center !important;
  text-transform: uppercase !important;
  letter-spacing: 0.5px !important;
  font-size: 12px !important;
  border: 1px solid #d0d7de !important;
  padding: 12px 8px !important;
  min-width: 100px !important;
  width: 100px !important;
  white-space: nowrap !important;
}

.custom-spreadsheet .Spreadsheet__header--selected {
  background-color: #e3f2fd !important;
  color: #24292f !important;
}

.custom-spreadsheet .Spreadsheet__row-header {
  background-color: #fafbfc !important;
  color: #24292f !important;
  font-weight: 600 !important;
  text-align: left !important;
  border: 1px solid #d0d7de !important;
  padding: 8px 12px !important;
  min-width: 120px !important;
  width: 120px !important;
  white-space: nowrap !important;
}

.custom-spreadsheet .Spreadsheet__row-header--selected {
  background-color: #e3f2fd !important;
  color: #24292f !important;
}

/* Zoom Slider Styles */
input[type="range"] {
  -webkit-appearance: none;
  appearance: none;
  background: transparent;
  cursor: pointer;
}

input[type="range"]::-webkit-slider-track {
  background: #e1e5e9;
  height: 4px;
  border-radius: 2px;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  background: #0366d6;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  cursor: pointer;
}

input[type="range"]::-moz-range-track {
  background: #e1e5e9;
  height: 4px;
  border-radius: 2px;
  border: none;
}

input[type="range"]::-moz-range-thumb {
  background: #0366d6;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  cursor: pointer;
  border: none;
}

/* Prevent touch actions on zoom controls */
.zoom-controls {
  touch-action: none !important;
  -webkit-touch-callout: none !important;
  -webkit-user-select: none !important;
  -khtml-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}

.zoom-controls * {
  touch-action: none !important;
  -webkit-touch-callout: none !important;
  -webkit-user-select: none !important;
  -khtml-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}